import React from 'react'
import { useNavigate } from 'react-router-dom';
import "./HomeMenu.css"

function HomeMenu() {
  const navigate = useNavigate();

  return (
    <div className='HomeMenu'>
      <div className='home-title-block'>
        <p className="title">COOK_WITH</p>
        <p className="subtitle"></p>
      </div>
      <div className="menu-button-block">
        <button type="button"
          className="btn btn-outline-light menu-button" onClick={() => navigate('/nutrition')} to="/nutrition"></button>
        <button type="button"
          className="btn btn-outline-light menu-button" onClick={() => navigate('/ingredients')}></button>
        <button type="button"
          className="btn btn-outline-light menu-button" onClick={() => navigate('/contact')}></button>
      </div>
    </div>
  )
}

export default HomeMenu